import React, { Component, Fragment } from "react";
import Navbar from '../../components/Navbar/Navbar'
import { MDBContainer, MDBInput } from "mdbreact";
import Multislider from '../../components/Multislider/Multislider';
import Footer from '../../components/Footer/Footer';
import 'react-input-range/lib/css/index.css';
import './FilterPage.css';
import ScrollUpButton from "react-scroll-up-button"; //Add this line Here
import FittedImage from 'react-fitted-image';
import Grid from '../../components/Grid/Grid';
import FormLabel from '@material-ui/core/FormLabel';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Checkbox from '@material-ui/core/Checkbox';
import InputRange from 'react-input-range';
class FilterPage extends Component {


    state = {
        Samsung: true,
        Oneplus: true,
        Apple: true,
        pricerange: { min: 2, max: 1000 },
    }
    showtab(cityName) {
        console.log("sds")
        var i, tabcontent, tablinks;
        tabcontent = document.getElementsByClassName("tabcontent");
        for (i = 0; i < tabcontent.length; i++) {
            tabcontent[i].style.display = "none";
        }
        tablinks = document.getElementsByClassName("tablinks");
        for (i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }
        document.getElementById(cityName).style.display = "block";

    }

    handleChange = name => event => {
        this.setState({ ...this.state, [name]: event.target.checked });
    };
    render() {
        const imgstyle = { maxWidth: "100px", margin: "20px", maxHeight: "100px" }

        return (

            <Fragment>

                <ScrollUpButton />

                <Navbar></Navbar>


                <MDBContainer fluid className="home">

                    {/* Hot Categories */}
                    <div style={{ marginTop: "50px" }}>
                        <div className="row">

                            <div className="col-md-3 filterlayout">
                                <div>
                                <select className="browser-default custom-select">
                                        <option>Sort by</option>
                                        <option value="1">Price Ascending</option>
                                        <option value="2">Price Descending</option>
                                        <option value="3">Alphabet Ascending</option>
                                        <option value="3">Alphabet Descending</option>
                                    </select>
                                </div>
                                <div style={{marginTop:"40px"}}>
                                    <FormLabel component="legend" style={{ color: "black" }}>Brand Selection</FormLabel>

                                    <div className="row">
                                        <div className="col-md-6">
                                            <FormControl component="fieldset" className="filter">
                                                <FormGroup>
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Samsung} onChange={this.handleChange('Samsung')} value="gilad" />}
                                                        label="Samsung"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Apple} onChange={this.handleChange('Apple')} value="jason" />}
                                                        label="Apple"
                                                    />
                                                    <FormControlLabel
                                                        control={
                                                            <Checkbox checked={this.state.Oneplus} onChange={this.handleChange('Oneplus')} value="antoine" />
                                                        }
                                                        label="One Plus"
                                                    />
                                                </FormGroup>
                                                <FormHelperText></FormHelperText>
                                            </FormControl>

                                        </div>
                                        <div className="col-md-6">
                                            <FormControl component="fieldset" className="filter">
                                                <FormGroup>
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Samsung} onChange={this.handleChange('Samsung')} value="gilad" />}
                                                        label="Samsung"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Apple} onChange={this.handleChange('Apple')} value="jason" />}
                                                        label="Apple"
                                                    />
                                                    <FormControlLabel
                                                        control={
                                                            <Checkbox checked={this.state.Oneplus} onChange={this.handleChange('Oneplus')} value="antoine" />
                                                        }
                                                        label="One Plus"
                                                    />
                                                </FormGroup>
                                                <FormHelperText></FormHelperText>
                                            </FormControl>

                                        </div>
                                    </div>

                                </div>
                                <div >
                                    <FormLabel component="legend" style={{ color: "black" }}>Price range Selection</FormLabel>
                                    <div style={{ marginTop: "40px" }}>
                                        <InputRange
                                            maxValue={10000}
                                            minValue={0}
                                            value={this.state.pricerange}
                                            onChange={value => this.setState({ ...this.state, pricerange: { ...value } })} />
                                    </div>

                                </div>
                                <div style={{ marginTop: "40px" }}>
                                    <FormLabel component="legend" style={{ color: "black" }}>Type Selection</FormLabel>

                                    <div className="row">
                                        <div className="col-md-6">
                                            <FormControl component="fieldset" className="filter">
                                                <FormGroup>
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Samsung} onChange={this.handleChange('Samsung')} value="gilad" />}
                                                        label="Samsung"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Apple} onChange={this.handleChange('Apple')} value="jason" />}
                                                        label="Apple"
                                                    />
                                                    <FormControlLabel
                                                        control={
                                                            <Checkbox checked={this.state.Oneplus} onChange={this.handleChange('Oneplus')} value="antoine" />
                                                        }
                                                        label="One Plus"
                                                    />
                                                </FormGroup>
                                                <FormHelperText></FormHelperText>
                                            </FormControl>

                                        </div>
                                        <div className="col-md-6">
                                            <FormControl component="fieldset" className="filter">
                                                <FormGroup>
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Samsung} onChange={this.handleChange('Samsung')} value="gilad" />}
                                                        label="Samsung"
                                                    />
                                                    <FormControlLabel
                                                        control={<Checkbox checked={this.state.Apple} onChange={this.handleChange('Apple')} value="jason" />}
                                                        label="Apple"
                                                    />
                                                    <FormControlLabel
                                                        control={
                                                            <Checkbox checked={this.state.Oneplus} onChange={this.handleChange('Oneplus')} value="antoine" />
                                                        }
                                                        label="One Plus"
                                                    />
                                                </FormGroup>
                                                <FormHelperText></FormHelperText>
                                            </FormControl>

                                        </div>
                                    </div>

                                </div>

                            </div>
                            <div className="col-md-9">
                                <Grid>
                                </Grid>
                                <Grid>
                                </Grid>
                                <Grid>
                                </Grid>
                            </div>
                        </div>

                    </div>

                    <div style={{ marginTop: "50px" }}>

                        <div className="col-md-12">

                            <div className="tab">
                                <button className="tablinks active" onClick={() => this.showtab('itemdesc')}>New Arrivals</button>
                                <button className="tablinks" onClick={() => this.showtab('reviews')}>Bestselling</button>
                                <button className="tablinks" onClick={() => this.showtab('shoppingtime')}>Super Deals</button>
                            </div>

                            <div id="itemdesc" style={{ display: "block" }} className="tabcontent">
                                <Grid>

                                </Grid>
                            </div>

                            <div id="reviews" className="tabcontent">
                                <Grid>

                                </Grid>
                                <Grid>

                                </Grid>    </div>

                            <div id="shoppingtime" className="tabcontent">
                                <Grid>

                                </Grid>
                            </div>

                        </div>

                    </div>
                    {/* Customers Who Bought This Item Also Bought */}
                    <div style={{ marginTop: "100px" }}>
                        <h4 style={{ margin: "5px" }}>Hot-sale Products</h4>

                        <div style={{ background: "white" }}>

                            <Multislider sponsers={false} picheight={"150px"} height={"250px"} width={"200px"} numbers={4}>

                            </Multislider>
                        </div>
                    </div>
                    {/* Similar Items */}
                    <div style={{ marginTop: "100px" }}>
                        <h4 style={{ margin: "5px" }}>Other items you might be interested in</h4>

                        <div style={{ background: "white" }}>

                            <Multislider sponsers={false} picheight={"150px"} height={"250px"} width={"200px"} numbers={4}>

                            </Multislider>
                        </div>
                    </div>


                </MDBContainer>

                <Footer></Footer>
            </Fragment >

        );
    }
}

export default FilterPage;